import React, { useEffect } from "react";
import { gsap, Power1 } from "gsap/all";
import SectionTitle from "./SectionTitle";
import styled from "styled-components";
import fifaDraw from "../img/project1.png";
import hairstyleWebsite from "../img/project2.png";
import changingRoom from "../img/project3.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import { faCode } from "@fortawesome/free-solid-svg-icons";
import starsPattern from "../img/stars-pattern.png";
import { projectsSectionContentAnimation } from "../animations/contentAnimations";
import { sectionTitleAnimation } from "../animations/sectionTitleAnimation";

const Wrapper = styled.section`
  position: relative;
  background-image: url(${starsPattern});
  background-repeat: no-repeat;
  background-size: 95%;
  background-position: 644% 40%;
  padding: 20px;
  overflow: hidden;
  z-index: 1;

  ::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgb(4, 5, 25);
    background: linear-gradient(
      0deg,
      rgba(4, 5, 25, 1) 0%,
      rgba(4, 5, 25, 0.7) 25%,
      rgba(4, 5, 25, 0.7) 75%,
      rgba(4, 5, 25, 1) 100%
    );
  }
`;

const Content = styled.div`
  position: relative;
  z-index: 1;
`;

const ProjectsList = styled.ul`
  list-style: none;
  height: 80%;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  li {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    pointer-events: none;
    margin: 35px 0;

    div {
      position: relative;
      pointer-events: auto !important;
      visibility: hidden;

      h3 {
        position: absolute;
        top: 50%;
        left: -22%;
        transform: translateY(-50%);
        color: #f2f2f2;
        font-family: "Montserrat", sans-serif;
        font-weight: 600;
        font-size: 14px;
        font-style: italic;
        pointer-events: none;
        visibility: hidden;
      }

      img {
        pointer-events: none;
        width: 250px;
      }
    }

    .links {
      position: relative;
      display: none;

      a {
        width: 52.5px;
        text-align: center;
        position: absolute;
        color: #f2f2f2;
        top: 50%;
        left: 50%;
        font-size: 42px;
        transform: translate(-140%, -50%);
      }

      a:nth-of-type(2) {
        transform: translate(40%, -50%);
      }
    }
  }

  li:nth-child(even) {
    justify-content: flex-start;

    div {
      ::before {
        background: rgb(4, 5, 25);
        background: linear-gradient(
          263deg,
          rgba(4, 5, 25, 1) 0%,
          rgba(4, 5, 25, 0.4) 45%
        );
      }

      h3 {
        left: auto;
        right: -22%;
      }
    }
  }
`;

const Pillars = styled.div`
  position: absolute;
  display: flex;
  height: 100%;
  width: 80px;
  top: 0;
  left: 40px;
  justify-content: space-between;

  .pillar {
    position: relative;
    width: 25%;
    height: 100%;
    background-color: #b712c3;

    ::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: #b712c3;
      box-shadow: 0px 0px 18px 6px rgba(183, 18, 195, 1);
    }
  }
`;

const projects = [
  {
    key: "01",
    title: "fifa draw app",
    img: fifaDraw,
    github: "https://github.com/mikulskee/fifa-draw",
    live: "https://mikulskee.github.io/fifa-draw/"
  },
  {
    key: "02",
    title: "landing page template",
    img: hairstyleWebsite,
    github: "https://github.com/mikulskee/website-hairstyle",
    live: "https://mikulskee.github.io/website-hairstyle/"
  },
  {
    key: "03",
    title: "online shop template",
    img: changingRoom,
    github: "https://github.com/mikulskee/ChangingRoom",
    live: "https://mikulskee.github.io/ChangingRoom/"
  }
];

const Projects = () => {
  const handleClick = e => {
    const tl = gsap.timeline();
    const description = e.target;

    const links = e.target.nextSibling;

    tl.set(links, { rotationX: -90 })
      .to(description, {
        duration: 0.1,
        rotationX: 90,
        ease: Power1.easeOut
      })
      .set(description, { display: "none" })
      .set(links, { display: "block" })
      .to(links, { duration: 0.1, rotationX: 0 })
      .to(links, { duration: 0.1, rotationX: -90, delay: 2.5 })
      .set(links, { display: "none" })
      .set(description, { display: "block" })
      .to(description, {
        duration: 0.1,
        rotationX: 0,
        ease: Power1.easeOut
      });
  };

  const newProjects = projects.map(item => (
    <li key={item.key}>
      <div onClick={handleClick} className={`project ${item.key}`}>
        <h3 className="project-title">{`${item.key}. ${item.title}`}</h3>
        <img src={item.img} alt={item.title} />
      </div>
      <div className={`project links ${item.key}`}>
        <img src={item.img} alt={item.title} />
        <a href={item.github}>
          {" "}
          <FontAwesomeIcon icon={faGithub} />
        </a>
        <a href={item.live}>
          {" "}
          <FontAwesomeIcon icon={faCode} />
        </a>
      </div>
    </li>
  ));

  useEffect(() => {
    const title = document.querySelector(".projects-section-title");
    const timeline = projectsSectionContentAnimation();

    let executed = false;

    window.addEventListener("scroll", () => {
      const height = window.innerHeight;
      const bottom = title.getBoundingClientRect().bottom;

      if (!executed && height >= bottom) {
        executed = true;
        timeline.play();
        sectionTitleAnimation(title);
      } else return;
    });
  });

  return (
    <Wrapper className="projects">
      <Content>
        <SectionTitle className={"projects-section-title section-title"}>
          projects
        </SectionTitle>

        <ProjectsList>{newProjects}</ProjectsList>
      </Content>
      <Pillars>
        <div className="pillar" />
        <div className="pillar" />
      </Pillars>
    </Wrapper>
  );
};

export default Projects;
