import React, { useEffect, useState } from "react";
import Header from "../components/Header";
import Main from "../components/Main";
import Contact from "../components/Contact";
import Footer from "../components/Footer";
import Nav from "../components/Nav";
import debounce from "lodash.debounce";
import { hideNav, showNav } from "../animations/contentAnimations";
const MainTemplate = () => {
  const [touchEnd, setTouchEnd] = useState();
  const [touchStart, setTouchStart] = useState();

  useEffect(() => {
    window.addEventListener(
      "wheel",
      debounce(e => {
        if (e.deltaY > 0) {
          const timeline = hideNav();
          timeline.play();
        } else {
          const timeline = showNav();
          timeline.play();
        }
      }, 50)
    );
  }, []);

  // useEffect(
  //   debounce(() => {
  //     window.addEventListener("touchstart", e => {
  //       setTouchStart(e.changedTouches[0].clientY);
  //     });
  //     window.addEventListener("touchend", e => {
  //       setTouchEnd(e.changedTouches[0].clientY);
  //     });

  //     if (touchStart > touchEnd && touchStart - touchEnd > 30) {
  //       const timeline = hideNav();
  //       timeline.play();
  //     } else if (touchStart < touchEnd && touchEnd - touchStart > 30) {
  //       const timeline = showNav();
  //       timeline.play();
  //     }
  //   }, 50),
  //   [touchEnd, touchStart]
  // );

  return (
    <>
      <Nav />
      <Header />
      <Main />
      <Contact />
      <Footer />
    </>
  );
};

export default MainTemplate;
