import React, { useState } from "react";
import styled from "styled-components";
import Burger from "./Burger";
import logo from "../img/logo.png";
import {
  openBurgerAnimation,
  closeBurgerAnimation
} from "../animations/contentAnimations";

const Wrapper = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 4;
  height: 0;
  transform: translateY(0);
  ul {
    transform: translateY(-120%);
    margin: 50px 0 30px;
    padding: 45px 0 0;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgb(4, 5, 25);
    box-shadow: 0px 20px 40px 30px rgba(4, 5, 25, 1);

    li {
      margin: 10px 0;
      button {
        font-family: "Montserrat", sans-serif;
        font-size: 14px;
        font-weight: bold;
        font-style: italic;
        letter-spacing: 1px;
        background: none;
        border: none;
        color: #f2f2f2;
        padding: 10px 15px;
      }
    }
  }
`;

const TopBar = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  padding: 20px 20px;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 5;
  background: rgb(4, 5, 25);
  box-shadow: 0px 7px 40px 30px rgba(4, 5, 25, 1);

  img.logo {
    width: 130px;
  }
`;
const Nav = () => {
  const [isOpen, setIsOpen] = useState("closed");

  const handleClickAnimation = e => {
    e.preventDefault();
    console.log(isOpen);
    const timelineOpen = openBurgerAnimation();
    const timelineClose = closeBurgerAnimation();
    if (isOpen === "closed") {
      timelineOpen.play();
      setIsOpen("open");
    } else if (isOpen === "open") {
      timelineClose.play();
      setIsOpen("closed");
    }
  };

  const handleClick = e => {
    console.log(e.target);
  };
  return (
    <Wrapper>
      <TopBar>
        <img className="logo" src={logo} alt="logo" />
        <Burger handleClick={handleClickAnimation} />
      </TopBar>

      <ul className="nav-list">
        <li>
          <span role="img" aria-label="backhand index pointing right">
            &#x1F449;
          </span>
          <button onClick={handleClick}>Home</button>
        </li>
        <li>
          <button onClick={handleClick}>Projects</button>
        </li>
        <li>
          <button onClick={handleClick}>Skills</button>
        </li>
        <li>
          <button onClick={handleClick}>About Me</button>
        </li>
        <li>
          <button onClick={handleClick}>Contact</button>
        </li>
      </ul>
    </Wrapper>
  );
};

export default Nav;
